import React, {useState} from "react";
import Display from './Display';
const BoxGenerator = (props) => {
    const [color, setColor] = useState("");
    const [dimentions, setDimentions] = useState("");
    const [radius, setRadius ] = useState("");
    const [boxes, setBoxes] = useState([]);
    
    const handleSubmit = (e) => {
        e.preventDefault();
        const newBox = { 
            color: color,
            dimentions: dimentions,
            radius: radius

        };
        console.log("My new box is", newBox);
        const copyBoxes = [...boxes];    
        copyBoxes.push(newBox);
        setBoxes(copyBoxes);
    }

    
    
    
    return(
        <div>
            <div className="w-80 mx-auto mt-1 p-2 rounded">
                <h6>Create A Box or Circle!</h6>    
                <form onSubmit = { handleSubmit }>
                    
                    <div className="form-group">
                        {/* <label className="h6" htmlFor="color">Color: </label> */}
                        <input placeholder="Color" className="form-control" id="input" onChange={ (e) => setColor(e.target.value) } type="text" name="color" />
                    </div>

                    <div className="form-group">
                        {/* <label className="h6" htmlFor="dimentions">Dimensions: </label> */}
                        <input placeholder="Dimensions" className="form-control" id="input" onChange={ (e) => setDimentions(e.target.value) } type="text" name="dimentions" />
                    </div>

                    <div className="form-group">
                        {/* <label className="h6" htmlFor="radius">Border Radius (Enter A Number Between 0 to 100 to Round Out Corners): </label> */}
                        <input placeholder="Border Radius Enter Number 0-100" className="form-control" id="input" onChange={ (e) => setRadius(e.target.value) } type="text" name="radius" />
                    </div>
                    
                    <button className="btn btn-outline-secondary btn-lg w-50 mt-2"  type="submit" value={"Box Dimentions"}>Create</button>
                </form>
            </div>           
            {/* <p>color:{JSON.stringify(color)}</p>
            <p>dimentions:{JSON.stringify(dimentions)}</p>
            <p>boxesArray:{JSON.stringify(boxes)}</p> */}
            {/* <hr/> */}
        <Display boxes = {boxes}/> 
        </div>
            

        
        
    );
};

export default BoxGenerator;